<template>
  <AppSidebar @emit-logout-button="logoutSidebar"></AppSidebar>
  <div class="dashboard-indent">
    <router-view></router-view>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

import AppSidebar from '@/components/AppSidebar.vue'
export default {
  components: {
    AppSidebar
  },
  methods: {
    ...mapMutations(['logout']),
    logoutSidebar () {
      if (window.confirm('Do you want to exit?')) {
        this.logout()
        this.$router.replace('/')
      }
    }
  }
}
</script>

<style scoped lang="scss">
.dashboard-indent {
  padding-left: 260px;
}
</style>
